.hero {
  width: 100%;
  height: 100vh;
  position: relative;
  color: white;
}
.overlay {
  background-color: rgba(0, 0, 0, 0.1);
  position: absolute;
  width: 100%;
  height: 100vh;
  top: 0;
  left: 0;
}
#video {
  width: 100%;
  height: 100vh;
  object-fit: cover;
  z-index: -1;
}
.hero .content {
  position: absolute;
  top: 0;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  margin: auto;
  padding: 1rem;
  text-align: center;
}
.form {
  display: flex;
  background-color: #ffffffcc;
  justify-content: space-between;
  align-items: center;
  color: black;
  border: 1px solid #ccc;
  border-radius: 8px;
  max-width: 700px;
  width: 100%;
  margin: 0 auto;
  padding: 4px;
}
.form input[type="text"] {
  background-color: transparent;
  border: transparent;
  font-size: 1.3rem;
  width: 300px;
  font-family: "Poppin", sans-serif;
}
.form input[type="text"]:focus {
  outline: none;
}

@media screen and (max-width: 940px) {
  .form input[type="text"] {
    width: 270px;
  }
}
