.beachname img {
  width: 100%;
  height: 100%;
  /* object-fit: cover; */
}
.beachname {
  position: relative;
}
.beachname .overlay {
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  position: absolute;
  background: rgba(0, 0, 0, 0.3);
}
.beachname p {
  position: absolute;
  bottom: 1rem;
  left: 1rem;
  color: white;
  font-size: 1.3rem;
  font-weight: 500;
}
