.navbar {
  width: 100%;
  height: 15vh;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 1rem;
  z-index: 2;
  position: absolute;
  color: white;
}
.navbar-bg {
  background-color: #f5f5f5;
  color: black;
}

.nav-menu {
  display: flex;
  padding: 0 1rem;
}
li {
  padding: 0 1rem;
  cursor: pointer;
}
.nav-menu li:hover {
  transform: scale(1.2);
  transition: 0.3s;
}
.navbar-icons .icon:hover {
  transform: scale(1.3);
  transition: 0.3s;
}
.hamburger {
  display: none;
}
.mobile-menu {
  display: none;
}

@media screen and (max-width: 940px) {
  .nav-menu {
    display: none;
  }
  .navbar-icons {
    display: none;
  }
  .hamburger {
    display: block;
  }
  .mobile-menu {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    top: -100vh;
    left: 0;
    position: absolute;
    background-color: #f5f5f5;
    color: black;
    transition: 0.3s;
  }
  .active {
    top: 80px;
  }
  .mobile-menu li {
    padding: 1.2rem 1rem;
    margin: 0 1rem;
    border-bottom: 1px solid #ccc;
  }
  .mobile-nav {
    margin: 1rem 0;
  }
  .mobile-nav-bottom {
    width: 100%;
    text-align: center;
    padding: 1rem 0;
  }
  .mobile-menu button {
    width: 90%;
    padding: 1rem 0;
    margin: 1rem 0;
  }
  .social-icons {
    display: flex;
    justify-content: space-around;
  }
  .social-icons .icon:hover {
    transform: scale(1.5);
    transition: 0.3s;
  }
}
