.footer {
  width: 100%;
  padding: 4rem 0;
  background-color: black;
  color: white;
}
.footer h3 {
  color: rgb(124, 124, 234);
}
.footer .container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}
.footer .social {
  display: flex;
  justify-content: space-between;
}
.social .icon:hover {
  transform: scale(1.5);
  transition: 0.5s;
}
